import type { Metadata } from "next";
import Link from "next/link";
import { STATE_REGULATIONS } from "@/data/states";

export const metadata: Metadata = {
  title: "US lending laws by state",
  description:
    "Browse installment, payday, and personal loan regulations for all 50 states plus D.C. Compare caps, cooling periods, and licensing requirements.",
};

export default function StatesIndex() {
  const grouped = STATE_REGULATIONS.reduce<Record<string, typeof STATE_REGULATIONS>>((acc, state) => {
    const letter = state.name[0];
    acc[letter] = acc[letter] ? [...acc[letter], state] : [state];
    return acc;
  }, {});

  const sortedKeys = Object.keys(grouped).sort();

  return (
    <div className="mx-auto max-w-6xl space-y-16 px-4 py-16 lg:px-8">
      <header className="space-y-4 text-center">
        <span className="rounded-full bg-white/70 px-4 py-2 text-xs font-semibold uppercase tracking-[0.3em] text-blue-400">
          All states
        </span>
        <h1 className="text-4xl font-semibold text-slate-900">
          Lending regulations by state
        </h1>
        <p className="mx-auto max-w-3xl text-base text-slate-500">
          Explore fee caps, cooling-off periods, and eligibility requirements per state. We track payday restrictions, installment loan ranges, and pre-qualification guidance for each jurisdiction.
        </p>
      </header>
      <div className="grid gap-8 md:grid-cols-2">
        {sortedKeys.map((letter) => (
          <section key={letter} className="space-y-4 rounded-3xl border border-slate-200 bg-white/80 p-6 shadow-sm">
            <h2 className="text-2xl font-semibold text-slate-800">{letter}</h2>
            <ul className="grid gap-2 text-sm text-slate-600">
              {grouped[letter]
                .sort((a, b) => a.name.localeCompare(b.name))
                .map((state) => (
                  <li key={state.abbr} className="flex items-center justify-between">
                    <div>
                      <Link
                        href={`/states/${state.abbr.toLowerCase()}`}
                        className="font-semibold text-slate-800 hover:text-blue-600"
                      >
                        {state.name}
                      </Link>
                      <p className="text-xs text-slate-500">
                        Payday {state.paydayAllowed ? "permitted" : "banned"} · Installment max ${state.installmentMaxAmount.toLocaleString()} · APR {state.typicalAprRange[0]}–{state.typicalAprRange[1]}%
                      </p>
                    </div>
                    <span className="rounded-full bg-blue-100 px-3 py-1 text-xs font-semibold text-blue-600">
                      {state.abbr}
                    </span>
                  </li>
                ))}
            </ul>
          </section>
        ))}
      </div>
    </div>
  );
}
